import { Pipe, PipeTransform } from "@angular/core";
import { City } from "./enums";

@Pipe({ name: "cityName" })
export class CityNamePipe implements PipeTransform {
    public transform(city: City): string {
        switch (city) {
            case City.Spb:
                return "-";
            case City.Msk:
                return "";
            case City.Sar:
                return "";
            case City.Kry:
                return "";
            case City.Kzn:
                return "";
            case City.Nsk:
                return "";
            case City.Nnv:
                return " ";
            case City.Ufa:
                return "";
        }

        const exhaustingCheck: never = city;
    }
}



